"use client";

import React, { useState, useRef } from "react";
import FullCalendar from "@fullcalendar/react";
import dayGridPlugin from "@fullcalendar/daygrid";
import timeGridPlugin from "@fullcalendar/timegrid";
import interactionPlugin from "@fullcalendar/interaction";
import {
    DateSelectArg,
    EventClickArg,
    EventContentArg,
    EventInput,
} from "@fullcalendar/core";
import { Modal } from "@/components/ui/modal";
import { useModal } from "@/hooks/useModal";
import PageBreadcrumb from "@/components/common/PageBreadCrumb";

/* ================= TYPES ================= */
interface CalendarEvent extends EventInput {
    extendedProps: {
        calendar: string;
        leaveType: string;
        reason?: string;
    };
}

/* ================= CONSTANTS ================= */
const leaveEventColors: Record<string, string> = {
    "Sick Leave": "danger",
    "Planned Leave": "primary",
    "Optional Holiday": "warning",
    "Work from Home": "success",
};

/* ================= DATE HELPERS ================= */
const addOneDay = (date: string) => {
    const d = new Date(date);
    d.setDate(d.getDate() + 1);
    return d.toISOString().split("T")[0];
};

const subtractOneDay = (dateInput: Date | string | number) => {
    const d = new Date(dateInput);
    d.setDate(d.getDate() - 1);
    return d.toISOString().split("T")[0];
};

/* ================= COMPONENT ================= */
export default function LeaveCalendarPage() {
    const calendarRef = useRef<FullCalendar>(null);
    const { isOpen, openModal, closeModal } = useModal();

    const [events, setEvents] = useState<CalendarEvent[]>([]);
    const [selectedEvent, setSelectedEvent] = useState<CalendarEvent | null>(null);

    const [leaveType, setLeaveType] = useState("");
    const [startDate, setStartDate] = useState("");
    const [endDate, setEndDate] = useState("");
    const [reason, setReason] = useState("");

    const [errors, setErrors] = useState<{
        leaveType?: boolean;
        startDate?: boolean;
        endDate?: boolean;
    }>({});

    /* ================= VALIDATION ================= */
    const validateForm = () => {
        const e: typeof errors = {};
        if (!leaveType) e.leaveType = true;
        if (!startDate) e.startDate = true;
        if (!endDate) e.endDate = true;
        setErrors(e);
        return Object.keys(e).length === 0;
    };

    /* ================= HANDLERS ================= */
    const handleDateSelect = (info: DateSelectArg) => {
        resetForm();
        setStartDate(info.startStr);
        setEndDate(info.startStr);
        openModal();
    };

    const handleEventClick = (info: EventClickArg) => {
        const evt = info.event as any;

        const start = evt.startStr;
        let end = start;

        if (evt.end) {
            end = subtractOneDay(evt.end);
        }

        setSelectedEvent(evt);
        setLeaveType(evt.extendedProps.leaveType);
        setStartDate(start);
        setEndDate(end);
        setReason(evt.extendedProps.reason || "");

        openModal();
    };

    const handleSave = () => {
        if (!validateForm()) return;

        const adjustedEndDate = addOneDay(endDate);

        if (selectedEvent) {
            setEvents((prev) =>
                prev.map((e) =>
                    e.id === selectedEvent.id
                        ? {
                            ...e,
                            title: leaveType,
                            start: startDate,
                            end: adjustedEndDate,
                            allDay: true,
                            extendedProps: {
                                calendar: leaveEventColors[leaveType],
                                leaveType,
                                reason,
                            },
                        }
                        : e
                )
            );
        } else {
            const newEvent: CalendarEvent = {
                id: Date.now().toString(),
                title: leaveType,
                start: startDate,
                end: adjustedEndDate,
                allDay: true,
                extendedProps: {
                    calendar: leaveEventColors[leaveType],
                    leaveType,
                    reason,
                },
            };
            setEvents((prev) => [...prev, newEvent]);
        }

        closeModal();
        resetForm();
    };

    const resetForm = () => {
        setSelectedEvent(null);
        setLeaveType("");
        setStartDate("");
        setEndDate("");
        setReason("");
        setErrors({});
    };

    /* ================= RENDER ================= */
    return (
        <div>
            <PageBreadcrumb pageTitle="Calendar" />

            <div className="rounded-2xl border border-gray-200 bg-white p-4 dark:border-gray-800 dark:bg-white/[0.03]">
                <FullCalendar
                    ref={calendarRef}
                    plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
                    initialView="dayGridMonth"
                    selectable
                    events={events}
                    select={handleDateSelect}
                    eventClick={handleEventClick}
                    eventContent={renderEventContent}
                    headerToolbar={{
                        left: "prev,next applyLeave",
                        center: "title",
                        right: "dayGridMonth,timeGridWeek,timeGridDay",
                    }}
                    customButtons={{
                        applyLeave: {
                            text: "Apply Leave +",
                            click: openModal,
                        },
                    }}
                />
            </div>

            {/* ================= MODAL ================= */}
            <Modal isOpen={isOpen} onClose={closeModal} className="max-w-lg p-6">
                <h2 className="mb-6 text-xl font-semibold text-gray-800 dark:text-white">
                    {selectedEvent ? "Edit Leave" : "Apply Leave"}
                </h2>

                {/* Leave Type */}
                <div className="mb-4">
                    <label className="text-sm font-medium">Leave Type *</label>
                    <select
                        value={leaveType}
                        onChange={(e) => setLeaveType(e.target.value)}
                        className={`mt-1 w-full rounded-lg border px-3 py-2 ${errors.leaveType ? "border-red-500" : "border-gray-300"
                            }`}
                    >
                        <option value="">Select Leave Type</option>
                        {Object.keys(leaveEventColors).map((type) => (
                            <option key={type}>{type}</option>
                        ))}
                    </select>
                </div>

                {/* Dates */}
                <div className="grid grid-cols-2 gap-4">
                    <div>
                        <label className="text-sm font-medium">Start Date *</label>
                        <input
                            type="date"
                            value={startDate}
                            onChange={(e) => setStartDate(e.target.value)}
                            className={`mt-1 w-full rounded-lg border px-3 py-2 ${errors.startDate ? "border-red-500" : "border-gray-300"
                                }`}
                        />
                    </div>
                    <div>
                        <label className="text-sm font-medium">End Date *</label>
                        <input
                            type="date"
                            value={endDate}
                            onChange={(e) => setEndDate(e.target.value)}
                            className={`mt-1 w-full rounded-lg border px-3 py-2 ${errors.endDate ? "border-red-500" : "border-gray-300"
                                }`}
                        />
                    </div>
                </div>

                {/* Reason */}
                <div className="mt-4">
                    <label className="text-sm font-medium">Reason</label>
                    <textarea
                        value={reason}
                        onChange={(e) => setReason(e.target.value)}
                        rows={3}
                        className="mt-1 w-full rounded-lg border border-gray-300 px-3 py-2"
                    />
                </div>

                {/* Actions */}
                <div className="mt-6 flex justify-end gap-3">
                    <button
                        onClick={closeModal}
                        className="rounded-lg border px-4 py-2 text-sm"
                    >
                        Cancel
                    </button>
                    <button
                        onClick={handleSave}
                        className="rounded-lg bg-blue-600 px-4 py-2 text-sm text-white hover:bg-blue-700"
                    >
                        {selectedEvent ? "Update Leave" : "Apply Leave"}
                    </button>
                </div>
            </Modal>
        </div>
    );
}

/* ================= EVENT UI ================= */
const renderEventContent = (info: EventContentArg) => {
    const color = info.event.extendedProps.calendar;
    return (
        <div
            className={`event-fc-color fc-bg-${color} rounded-md px-2 py-1 text-xs font-medium text-white`}
        >
            {info.event.title}
        </div>
    );
};
